// Friends.scss
@import url(Common.scss);
$kakaoColor: #513736;
$borderColor: #ebebeb;
$fontColor: #8e9399;

.profile_list {
  height: 32px;
  padding-left: 15px;
  line-height: 32px;
  border: 1px solid $borderColor;
  border-left: none;
  border-right: none;
  color: #b6b5b1;
}
ul.profile_lists {
  display: flex;
  flex-direction: column;
  li {
    border-bottom: 0;
    display: flex;
    align-items: center;
    a {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 14px;
    }
  }
}

.profile_name {
  flex: auto;
}

.profile_message {
  position: absolute;
  right: 0;
  height: 30px;
  margin-right: 15px;
  padding: 0 10px;
  border-radius: 5px;
  background: #f0f0f0;
  font-size: 10px;
  color: #8e9399;
  line-height: 30px;
}

.friends_tab .tab_bar > ul > li:nth-child(1) > a {
  color: $kakaoColor;
}

main {
  height: 85.8%;
  padding-top: 64px;
}