// Chatting.scss
@import url(Common.scss);
$backgroudColor: #a1c0d5;
$borderColor: #96acba;

/* header */
.chatting_header header {
  position: absolute;
  color: #000; 
  background-color: transparent; 
  border-bottom: transparent;
  .title_bar {
    .left_item>a {
      color: #000; 
      opacity: 1; 
      font-size: 24px;
    }
    .right_item>a {
        color: #000; 
        font-size: 16px;
        i {
          padding-left: 5px;
        }
    }
}
}

/* main */
body {
  width: calc(100vw - calc(100vw - 100%));
}
main.chatting {
  display: flex; 
  flex-direction: column; 
  // height: calc(100vh - 109px);
  padding-top: 64px; 
  background: #a1c0d5;
  .date_info {
    opacity: 0.5; 
    text-align: center; 
    margin: 10px;
  }
  .chat_box {
    position: relative;
    display: inline-flex; 
    flex-direction: column; 
    align-items: flex-start; 
    width: fit-content; 
    max-width: calc(100% - 80px); 
    .chat {
      font-size: 14px; 
      line-height: 1; 
      margin: 5px; 
      padding: 10px 5px; 
      border-radius: 5px; 
      background: #fff;
    }
  }
  .my {
    align-self: flex-end; 
    align-items: flex-end; 
    max-width: 240px; 
    margin-right: 10px;
    .chat {
      background-color: #ffe73a;
    }
    .chat_time {
      position: absolute; 
      bottom: -5px; 
      left: -30px; 
      width: 50px; 
      opacity: 0.5;}
  }
  
  .other {
    margin-left: 50px; 
    margin-top: 20px;
    .profile_img {
      position: absolute; 
      top: -5px; 
      left: -55px; 
      width: 35px; 
      height: 35px;
    }
    .profile_name {
      display: block; 
      font-weight: bold; 
      margin: 5px;
    }
    .chat_time {
      position: absolute; 
      right: -30px; 
      bottom: -5px;
      width: 50px; 
      opacity: 0.5;
    } 
  }
}

