// Footer.scss
@import url(Common.scss);


footer {
  display: flex; 
  justify-content: space-between; 
  align-items: center; 
  position: fixed; 
  bottom: 0; 
  left: 0; 
  width: 100%; 
  height: 45px; 
  background: #eee;

  .plus_btn a {
    color: #b3b4b4; 
    padding-left: 15px; 
    font-size: 16px;
  }
  form {
    width: calc(100% - 40px); 
    display: flex; 
    justify-content: flex-end;

    fieldset.text_box {
      position: relative; 
      display: flex; 
      justify-content: flex-end; 
      align-items: center; 
      width: 100%;

      .text_field {
        width: calc(100% - 40px);
        height: 32px;
      }
      .emoticon_btn {
        position: absolute; 
        right: 45px;
        a {
          color: #b3b4b4;
        }
      }
      .voice_btn a {
        display: block; 
        width: 34px; 
        height: 37px; 
        margin-right: 5px; 
        background: #ffe73a; 
        font-size: 16px; 
        color: #513736; 
        line-height: 37px; 
        text-align: center;
      }
    }
  }
}